*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 10px;
}

body {
  min-width: 100vw;
  min-height: 100vh;
  color: #fff;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
  display: flex;
}

.container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  margin: 4rem 0;
  display: flex;
}

.container > div {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 6rem;
  padding: 3rem;
  display: flex;
}

.screen {
  width: 20rem;
  height: 10rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  background-color: #101010;
  outline: 1px solid #ffffff40;
  padding: 10px;
  font-family: monospace;
  overflow: hidden;
}

.inputDiv {
  gap: 5rem;
  display: flex;
}

textarea {
  resize: none;
  padding: 1rem;
}

.inputDiv > div {
  flex-direction: column;
  gap: 2rem;
  display: flex;
}

.inputDiv button {
  background-color: #fff;
  border: none;
  outline: none;
  padding: 1rem;
}

.mobile {
  display: none;
}

@media only screen and (max-width: 800px) {
  .container {
    display: none;
  }

  .mobile {
    text-align: center;
    padding: 2rem;
    font-family: monospace;
    display: block;
  }
}

